import React from "react"
import {  graphql , Link} from 'gatsby'
import styled, { keyframes } from "styled-components"
import posed from "react-pose";
import nav_up from "../images/nav_up.svg"
import nav_close from "../images/nav_close.svg"
import nav_down from "../images/nav_down.svg"
import {ReactComponent as ChainIcon} from "../images/chain.svg"
import { DOMParser } from "xmldom"
import {LinkWithIcon} from "../components/style"
import pointer from "../images/pointer.svg"


const SideBar = styled.div`
  width: 0;
  display: inline-block;
  vertical-align: top;
  text-align: left;
  background-color: #fefefe;
  position: sticky;
  top: 98px;
`

const PointerIcon = styled.img`
  height: 17px;
  vertical-align: middle;
  display: inline-block;
  transform: rotate(180deg);
`

const SideBarTitle = styled.div`
  background-color: #748cc5;
  color: #fafafa;
  display: inline-block;
  width:30px;
  min-height: 150px;
`

const SideBarTitleText = styled.span`
  transform:rotate(90deg);
  display: block;
  margin-top: 15px;
  letter-spacing: 2px;
`

const SideBarContent = styled.div`
  padding: 5px 0;  
  ul {
    list-style: none;
    text-align: left;
    margin: 0 auto;
    padding-left: 3px;
  }
  li {
    padding: 0 7px;
    a {
      border: 0;
    }
  }
`

const ContentLink = styled.div`
  width: 178px;
  height: 25px;
  text-decoration: none;
  display: inline-block;
`

const TableOfContent = styled.div`
  width: 218px;
  display: inline-block;
  border: 1px solid rgba(104,115,219,0.56);
`

const SideBarAnimator = posed(SideBar)({
  enter: {
    x: 0,
    opacity: 1,
    transition: { ease: 'easeOut', duration: 800 }
  },
  exit: {
    x: -600,
    opacity: 0,
    transition: { ease: 'easeOut', duration: 800 }
  },
})

const SideBarWrapper = styled.div`
  width: 250px;
  margin-left: -260px;
  flex-wrap: wrap;
  display: inline-flex;
`

const PostContainer = styled.div`
  width: 99%;
  display: inline-block;
`

const RightToLeft = posed(PostContainer)({
  enter: {
    x: 0,
    opacity: 1,
    transition: { ease: 'easeOut', duration: 800 }
  },
  exit: {
    x: 400,
    opacity: 0,
    transition: { ease: 'easeIn', duration: 800 }
  },
})

const rtl = keyframes`
  0% {
    opacity: 0;
    transform: translateX(400px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
`;

const ltr = keyframes`
  0% {
    opacity: 0;
    transform: translateX(-400px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
`;


const PostContain = styled(RightToLeft)`
   animation: ${rtl} 1s ease-out 0s forwards;
`

const TosContain = styled(SideBarAnimator)`
 animation: ${ltr} 1s ease-out 0s forwards;
`

const Anchor= styled.div`
  width:0;
  height:0;
`

const TopAnchor = styled(Anchor)`
  position: absolute;
  top: -30px;
`

const PostFrame = styled.div`
  position: relative;
  min-height: 300px;
`

const PostContent = styled.div`
  margin: 0;
  display: inline-block;
  width: 100%;
  p {
   padding: 0 10px;
  }
`

const Navigator = styled.div`
  position: sticky;
  top: 98px;
  width: 0;
  display: inline-block;
  vertical-align: top;
`

const Nav = styled(Link)`
  background-color: #3c3c3c;
  width: 50px;
  height: 50px;
  margin-left: 20px;
  margin-bottom: 5px;
  color: #f1f1f1;
  cursor: pointer;
  background-position: center center;
  background-repeat: no-repeat;
  display: block;
    -webkit-box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  -moz-box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  border: 1px #999999 solid;
  
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    opacity: 0;
    -webkit-box-shadow: 2px 7px 10px rgba(0,0,0,.26);
    -moz-box-shadow: 2px 7px 10px rgba(0,0,0,.26);
    box-shadow: 2px 7px 10px rgba(0,0,0,.26);
    transition: opacity 0.2s ease-out;
  }

  &:hover{
    transform: translateY( -2px) scale(1.01);
    transition: transform 0.3s ease-out;
    &:after {
      opacity: 1;
      transition: opacity 0.2s ease-out;
    }
  }
  
`

const NavUp = styled(Nav)`
  background-image: url(${nav_up});
  background-color: #748cc5;
`

const NavClose = styled(Nav)`
  background-image: url(${nav_close});
  background-size: 70%;
  background-color: #cb586a;
`

const NavDown = styled(Nav)`
  background-image: url(${nav_down});
  background-color: #748cc5;
`
const Post = styled.div`
  display: inline-block;
  width: 100%;
`

const PagingFooter = styled.div`
  width: 100%;
  z-index: 5;
  bottom: 0;
  left: 0;
  position: fixed;
  text-align: center;
  background-color: #f1f1f1;
  border-top: 1px solid #bebebe;
`

