// `


class Commento extends React.Component {
  constructor(...args) {
    super(...args)
    this.ref = React.createRef()
  }
  render() {
    return (
      <div>
        <div id="commento"> </div>
        <div ref={this.ref} />
      </div>
    )
  }
  componentDidMount() {
    var async = true;
    const s = document.createElement('script')
    s.async = async;
    s.src = 'https://cdn.commento.io/js/commento.js'
    // s.setAttribute('data-timestamp', +new Date())
    this.ref.current.appendChild(s)
  }
}

const PostWrapper = styled.div`
  width: 800px;
  margin: 0 auto;
`

const ContentName = styled.span`
  vertical-align: middle;
`

const ContentNumber = styled.span`
  line-height: 28px;
  &:after{
    content:". ";
  }
`

export default ({ data, pageContext }) => {
  const post = data.markdownRemark
  return (
      <PostWrapper>
          <TopAnchor  id="top-anchor" />
          <PostFrame>
            <SideBarAnimator>
              <SideBarWrapper>
                <SideBarTitle><SideBarTitleText>INDEX</SideBarTitleText></SideBarTitle>
                <TableOfContent>
                  {(() => {
                    const toc = new DOMParser().parseFromString(post.tableOfContents,'text/html')
                    if (toc!==undefined){
                      const list = toc.getElementsByTagName("a")
                      let allData = []
                      let iterator = 0
                      for (let listData of Array.from(list)) {
                        let element = Array.from(listData.childNodes).map(el => el.nodeValue)
                        if (element.length>0) {
                          iterator++
                          let currentLi = React.createElement('li',{'key':'li_'+element[0]},[
                              React.createElement(LinkWithIcon,{'to':listData.getAttribute('href'), 'active':true},
                                React.createElement( ContentLink,{},[
                                  React.createElement(ChainIcon,{}),
                                  React.createElement(ContentNumber,{},iterator.toString().padStart(2,'0')),
                                  React.createElement(ContentName,{},element[0]),
                                ])
                              ),
                              React.createElement(PointerIcon,{'src':pointer})
                            ]

                          )
                          allData.push(currentLi);
                        }
                      }
                      return React.createElement(SideBarContent,{},
                        React.createElement('ul',{},allData)
                      );
                    }
                  })()}

                </TableOfContent>
              </SideBarWrapper>
            </SideBarAnimator>
            <RightToLeft >
              <Post>
                <h1>{post.frontmatter.title}</h1>
                <PostContent dangerouslySetInnerHTML={{ __html: post.html }}  />
              </Post>
              <Navigator>
                <NavUp to={post.fields.path+"/#top-anchor"} />
                <NavClose to={"/#"} onClick={(event)=>{window.history.back(); event.preventDefault();}}/>
                <NavDown to={post.fields.path+"/#bottom-anchor"} />
              </Navigator>
            </RightToLeft>
          </PostFrame>
          <Anchor id="bottom-anchor"/>
          <PagingFooter>
          </PagingFooter>
        <Commento/>
      </PostWrapper>
  )
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      tableOfContents(pathToSlugField: "fields.path")
      frontmatter {
        title
        url
      }
      fields {
        path
      }
    }
  }
`

if (typeof window !== "undefined") {
  // eslint-disable-next-line global-require
  require("smooth-scroll")('a[href*="#"]',{ speed: 150, easing: 'easeOutQuart', updateURL: false,})
}